@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  font-display: swap;
  src: url("../fonts/lato/Lato-Regular.woff2") format("woff2"), url("../fonts/lato/Lato-Regular.woff") format(".woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: medium;
  font-weight: 500;
  font-family: "Oswald";
  font-display: swap;
  src: url("../fonts/oswald/oswaldmedium.woff2") format("woff2"), url("../fonts/oswald/oswaldmedium.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  min-height: 100vh;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", sans-serif;
  color: #444444;
}

body {
  width: 100%;
  min-width: 320px;
  height: 100%;
  margin: 0;
  padding: 0;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0 0 0 0);
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.container {
  width: 100%;
  max-width: 1260px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    /* padding: 0 30px; */
    padding: 0 14px 0 30px;
  }
}
@media (min-width: 1260px) {
  .container {
    padding: 0 20px;
  }
}

.page-title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #000000;
}
@media (min-width: 768px) {
  .page-title {
    font-size: 60px;
    line-height: 60px;
    text-align: start;
  }
}

.button {
  display: inline-block;
  padding: 11px;
  font-weight: 400;
  font-size: 16px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover:not(.catalog-card__button--empty), .button:focus:not(.catalog-card__button--empty) {
  background-color: #5eaa2f;
}
.button:active:not(.catalog-card__button--empty) {
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 26px;
  }
}

.logo {
  display: block;
  width: fit-content;
  color: #111111;
  text-decoration: none;
  --color-text: #111111;
  fill: currentColor;
}
.logo--header {
  display: grid;
  align-items: center;
  color: #444444;
  column-gap: 56px;
  grid-template-columns: auto 1fr;
}
@media (min-width: 768px) {
  .logo--header {
    column-gap: 13px;
  }
}
.logo--header:hover, .logo--header:focus {
  opacity: 0.8;
}
.logo--header:active {
  opacity: 0.6;
}
.logo--footer {
  width: 128px;
  height: 24px;
}
.logo--footer:hover, .logo--footer:focus {
  color: #3e3e3e;
  --color-text: #3e3e3e;
}
.logo--footer:active {
  color: #6b6b6b;
  --color-text: #6b6b6b;
}

.logo__image {
  display: block;
  width: 33px;
  height: 38px;
}
@media (min-width: 768px) {
  .logo__image {
    width: 60px;
    height: 50px;
    margin-left: 9px;
  }
}
@media (min-width: 1260px) {
  .logo__image {
    width: 70px;
    height: 59px;
    margin-left: 0;
  }
}

.logo__text {
  display: block;
  width: 101px;
  height: 18px;
}
@media (min-width: 1260px) {
  .logo__text {
    width: 118px;
    height: 21px;
  }
}

.logo__text-footer {
  width: 128px;
  height: 24px;
}

.page-header {
  position: relative;
}

@media (min-width: 1260px) {
  .page-header--background::after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: -13px;
    z-index: -1;
    width: 50%;
    background-color: rgba(104, 183, 56, 0.85);
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/promo-cat-desktop.jpg");
    background-repeat: repeat, no-repeat;
    object-fit: cover;
  }
}
@media (min-width: 1260px) and (min-resolution: 2dppx) {
  .page-header--background::after {
    background-image: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/promo-cat-desktop-2-@2x.jpg");
    background-size: auto, cover;
  }
}
.page-header__container {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  min-height: 65px;
}
@media (min-width: 768px) {
  .page-header__container {
    padding: 25px 16px 0 20px;
  }
}
@media (min-width: 1260px) {
  .page-header__container {
    padding-top: 55px;
    padding-bottom: 41px;
  }
}

.nav-list {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
  border-top: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .nav-list {
    border: none;
  }
}

.nav-list__item {
  border-bottom: 1px solid #d9d9d9;
}
@media (min-width: 768px) {
  .nav-list__item {
    border: none;
  }
}
.nav-list__item--current {
  position: relative;
}
@media (min-width: 768px) {
  .nav-list__item--current::after {
    content: "";
    position: absolute;
    right: 3px;
    bottom: 0;
    left: 28px;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1260px) {
  .nav-list__item--current::after {
    content: "";
    position: absolute;
    right: 5px;
    bottom: 0;
    left: 31px;
    height: 2px;
    background: #68b738;
  }
}
@media (min-width: 1260px) and (min-width: 1260px) {
  .nav-list__item--current:first-child:after {
    background: #ffffff;
  }
}

.nav-list__link {
  display: block;
  padding: 22px;
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", sans-serif;
  text-align: center;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .nav-list__link {
    padding: 14px 4px 9px 29px;
    font-size: 18px;
    line-height: 24px;
    text-transform: uppercase;
  }
}
@media (min-width: 1260px) {
  .nav-list__link {
    padding: 12px 4px 9px 30px;
  }
}
.nav-list__link:hover, .nav-list__link:focus {
  opacity: 0.6;
}
.nav-list__link:active {
  opacity: 0.3;
}

@media (min-width: 1260px) {
  .page-header--background .nav-list__link {
    font-size: 20px;
    color: #ffffff;
  }
}

.header-nav__list {
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 4;
  display: none;
}
@media (min-width: 768px) {
  .header-nav__list {
    position: relative;
    display: flex;
  }
}
@media (min-width: 768px) and (min-width: 1260px) {
  .header-nav__list {
    background-color: transparent;
  }
}

.header-nav__toggle {
  position: relative;
  width: 24px;
  height: 24px;
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header-nav__toggle {
    display: none;
  }
}

.header-nav__toggle-decor {
  position: absolute;
  left: 0;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.header-nav__toggle-decor::before, .header-nav__toggle-decor::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transform: translate(-50%);
}
.header-nav__toggle-decor::before {
  top: 8px;
}
.header-nav__toggle-decor::after {
  top: -8px;
}

.header-nav--open .header-nav__toggle-decor {
  background-color: transparent;
}
.header-nav--open .header-nav__toggle-decor::before {
  top: 50%;
  transform: translate(-35%, -50%) rotate(-46deg);
}
.header-nav--open .header-nav__toggle-decor::after {
  top: 50%;
  transform: translate(-35%, -50%) rotate(46deg);
}

.header-nav--open .header-nav__list {
  display: block;
}
@media (min-width: 768px) {
  .header-nav--open .header-nav__list {
    display: flex;
  }
}

.header-nav--nojs {
  width: 100%;
}
@media (min-width: 768px) {
  .header-nav--nojs {
    width: auto;
  }
}

.header-nav--nojs .header-nav__list {
  position: static;
  display: block;
}
@media (min-width: 768px) {
  .header-nav--nojs .header-nav__list {
    position: relative;
    display: flex;
  }
}

.header-nav--nojs .header-nav__toggle {
  display: none;
}

