.catalog-card {
  margin: 0 -20px;
  margin-bottom: 48px;

  @media (min-width: $tablet-width) {
    margin: 0 0 91px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 100px;
  }
}

.catalog-card__item {
  position: relative;

  @media (min-width: $tablet-width) {
    padding-top: 81px;
  }
}

.catalog-card__item:nth-child(2n) {
  /* padding-top: 63px; */
}

.catalog-card__wrapper {
  display: grid;
  align-items: start;
  padding: 20px 20px 27px;

  gap: 11px 0;
  grid-template-columns: 140px 1fr;

  @media (min-width: $tablet-width) {
    padding: 29px 37px 40px;

    background-color: #f2f2f2;

    gap: 22px;
    grid-template-columns: 1fr;
  }

  @media (min-width: $desktop-width) {
    padding: 29px 38px 40px;

    gap: 11px;
  }
}

.catalog-card__item-link {
  display: block;
  width: 140px;
  height: 107px;

  @media (min-width: $tablet-width) {
    width: 200px;
    height: 210px;
    margin: 0 auto;
    margin: -100px auto 0;
  }

  @media (min-width: $desktop-width) {
    width: 169px;
    height: 210px;
  }
}

