    margin: -97px auto 0;
  }
} */

.catalog-card__image {
  display: block;
  width: 100%;
  height: 100%;
}

.catalog-card__link {
  color: #222222;
  text-decoration: none;
}

.catalog-card__title {
  margin: 0 0 14px;

  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  font-family: $font-oswald;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    font-size: 20px;
    line-height: 24px;
    text-align: center;
  }
}

.catalog-card__table {
  margin: 0;
  padding: 15px 0 8px;

  font-size: 12px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    padding: 20px 0 3px;

    font-size: 16px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 20px;
  }
}

.catalog-card__table-item {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  gap: 10px;

  @media (min-width: $tablet-width) {
    padding-bottom: 11px;

    border-bottom: 1px solid #d9d9d9;
  }
}

.catalog-card__table dd {
  margin: 0;
}

.catalog-card__button {
  width: 100%;
  padding: 8px;

  grid-column: 1/-1;

  @media (min-width: $tablet-width) {
    display: inline-block;
    max-width: 254px;
    margin: 0 auto;
    padding: 10px 28px 10px;

    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    font-family: Oswald;
  }
}

.catalog-card__item--more {
  .catalog-card__wrapper {
    position: relative;

    display: block;
    padding: 77px 20px 15px;

    text-align: center;

    @media (min-width: $tablet-width) {
      padding: 163px 37px 40px;
    }

    @media (min-width: $desktop-width) {
      padding: 150px 37px 38px;
    }
  }
}

.catalog-card__item--more::before,
.catalog-card__item--more::after {
  content: "";
  position: absolute;
  top: 25px;
  left: 50%;

  width: 2px;
  height: 30px;

  background-color: #d3d3d3;

  @media (min-width: $tablet-width) {
    top: 133px;
    z-index: 2;

    height: 60px;
  }
}

.catalog-card__item--more::after {
  transform: rotate(90deg);
}

