.catalog-card__title {
  margin-bottom: 0;

  @media (min-width: $tablet-width) {
    max-width: 136px;
    margin: 0 auto;

    br {
      display: none;
    }
  }
}

.catalog-card__text {
  margin: 0 0 20px;
  padding-top: 8px;

  font-size: 12px;
  line-height: 16px;

  @media (min-width: $tablet-width) {
    margin: 0 37px 80px;
    padding-top: 23px;

    font-size: 16px;
    line-height: 18px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 89px;
    padding-top: 26px;
  }
}

.catalog-card__button {
  color: #ffffff;

  background-color: #68b738;
}

.catalog-card__button--empty {
  color: #444444;

  background-color: #f2f2f2;
}
